header {
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  width: 100%;
}

.nav {
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

ul.menu {
  display: flex;
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  color: #4a4a4a;
  padding-inline-start: 0px
}
ul.menu li a {
  float: left;
  display: block;
  color: #4a4a4a;
  text-align: center;
  padding: 16px 18px;
  text-decoration: none;
}
ul.menu li a:hover {
  text-decoration: underline;
}

ul.menu a.active {
  font-weight: bold;
}

.button-xs {
  background-color:#004777;
  color: white !important;
  margin: 10px 18px 16px 18px;
  padding: 4px 10px !important;
  border: 1px transparent;
  border-radius: 4px;
  text-decoration: none;
  font-size: 0.9em;
}

.logo {
  cursor: pointer;
  margin: 16px 18px;
  font-family: 'Pacifico', cursive;
  font-size: 18px
  
}

@media screen and (max-width: 600px) {
  ul.Header li a {
    float: none;
  }
  header {
    position: relative;
  }
  .logo{
    text-align: center;
  }
  .nav {
    flex-direction: column;
    margin-top: 0;
    margin-bottom: -72px;
  }
  
  ul.menu {
    display: inline-grid;
    justify-items: center;
    padding: 0;
  }
}